  font-size: 1em;
  font-family: FontAwesome;
  display: inline-block;
  content: "\f005";
}

.rating10 > .half:before { 
  content: "\f089";
  position: absolute;
}

.rating10 > label { 
  color: #ddd; 
 float: right; 
}


.rating10 > input:checked ~ label,
.rating10:not(:checked) > label:hover, 
.rating10:not(:checked) > label:hover ~ label { color: #FFD700;  } 

.rating10 > input:checked + label:hover, 
.rating10 > input:checked ~ label:hover,
.rating10 > label:hover ~ input:checked ~ label, 
.rating10 > input:checked ~ label:hover ~ label { color: #FFED85;  } 



a[data-tooltip] {
  position: relative;
}
a[data-tooltip]::before,
a[data-tooltip]::after {
  position: absolute;
  display: none;
  opacity: 0.85;
}
a[data-tooltip]::before {
 
  content: attr(data-tooltip);
  background: #000;
  color: #fff;
  font-size: 13px;
  padding: 5px;
  border-radius: 5px;
  
  white-space: nowrap;
  text-decoration: none;
}
a[data-tooltip]::after {
  width: 0;
  height: 0;
  border: 6px solid transparent;
  content: '';
}

a[data-tooltip]:hover::before,
a[data-tooltip]:hover::after {
  display: block;
}

a[data-tooltip][data-placement="top"]::before {
  bottom: 100%;
  left: 0;
  margin-bottom: 40px;
}
a[data-tooltip][data-placement="top"]::after {
  border-top-color: #000;
  border-bottom: none;
  bottom: 50px;
  left: 20px;
  margin-bottom: 4px;
  width: 10%;
}
