* {
    font-family: 'Poppins', sans-serif;
}

img {
    -webkit-user-drag: none;
    -moz-user-drag: none;
    -o-user-drag: none;
}

img {
    pointer-events: none;
}

.movie_card {
    padding: 0 !important;
    margin: 14px;
    border-radius: 10px;
    box-shadow: 0 3px 4px 0 rgba(0, 0, 0, 0.2), 0 4px 15px 0 rgba(0, 0, 0, 0.19);
    height: 95%;
}

.movie_card:hover {
    transform: scale(1.05);
    box-shadow: 0 0 40px #AAA;
}

.movie_card img {
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    height: 20rem;
}

.movie_info {
    color: #5e5c5c;
    padding-left: 75px;
}

.movie_info i {
    font-size: 20px;
}

.card-title {
    width: 80%;
    height: 4rem;
}

.play_button {
    background-color: #ff3d49;
    position: absolute;
    width: 60px;
    height: 60px;
    border-radius: 50%;
    right: 20px;
    bottom: 111px;
    font-size: 27px;
    padding-left: 21px;
    padding-top: 16px;
    color: #FFFFFF;
    cursor: pointer;
}